@use "../common/variables.scss" as *;
@use "../common/mixins" as *;

.search {
  form {
    position: relative;
  }

  input {
    opacity: 0;
    visibility: hidden;
    width: 0;
    height: 70px;
    border: transparent;
    border-radius: 20px;
    font-family: $basic-font;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    outline: none;
    padding: 10px;
    box-sizing: border-box;
    color: $color-text-black;
    background-color: transparent;
    transition: opacity 0.3s ease, visibility 0.3s ease, width 0.6s ease, background-color 0.6s ease;
    cursor: pointer;
  }

  button {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    background: none;
    border: none;
    cursor: pointer;
    box-sizing: border-box;

    &:hover {
      opacity: 0.7;
    }
  }

  svg {
    width: 100%;
    height: 100%;
    display: block;
  }
}

.search--active {
  input {
    opacity: 1;
    visibility: visible;
    width: 356px;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    color: $color-background-white;

    &::placeholder {
      color: $color-text-white;
    }
  }

  button svg {
    color: $color-background-white;
  }
}
